<template>
  <div class="glslCanvasContainer" ref="glslCanvasContainer">
    <GLSLCanvas :size="canvasSize"
                :vertex-shader="vertexShader"
                :fragment-shader="fragmentShader"
                :active-external-resources="currentImage"
                :quality="2" />
  </div>
</template>

import GLSLCanvas from "@/components/GLSLCanvas"

<script>
import GLSLCanvas from "@/components/GLSLCanvas"

const VERTEX_SHADER_SOURCE = `attribute vec4 position;
void main() {
    gl_Position = position;
}`

const FRAGMENT_SHADER_SOURCE = `precision highp float;

uniform vec2 resolution;
uniform float time;
uniform vec2 mouse;
uniform sampler2D backbuffer;
uniform sampler2D channel0;
uniform vec2 channelSize0;

#define PI 3.14159265

vec3 permute(vec3 x) { return mod(((x*34.0)+1.0)*x, 289.0); }

float snoise(vec2 v){
  const vec4 C = vec4(0.211324865405187, 0.366025403784439,
           -0.577350269189626, 0.024390243902439);
  vec2 i  = floor(v + dot(v, C.yy) );
  vec2 x0 = v -   i + dot(i, C.xx);
  vec2 i1;
  i1 = (x0.x > x0.y) ? vec2(1.0, 0.0) : vec2(0.0, 1.0);
  vec4 x12 = x0.xyxy + C.xxzz;
  x12.xy -= i1;
  i = mod(i, 289.0);
  vec3 p = permute( permute( i.y + vec3(0.0, i1.y, 1.0 ))
  + i.x + vec3(0.0, i1.x, 1.0 ));
  vec3 m = max(0.5 - vec3(dot(x0,x0), dot(x12.xy,x12.xy),
    dot(x12.zw,x12.zw)), 0.0);
  m = m*m ;
  m = m*m ;
  vec3 x = 2.0 * fract(p * C.www) - 1.0;
  vec3 h = abs(x) - 0.5;
  vec3 ox = floor(x + 0.5);
  vec3 a0 = x - ox;
  m *= 1.79284291400159 - 0.85373472095314 * ( a0*a0 + h*h );
  vec3 g;
  g.x  = a0.x  * x0.x  + h.x  * x0.y;
  g.yz = a0.yz * x12.xz + h.yz * x12.yw;
  return 130.0 * dot(m, g);
}

void main(void) {
    vec2 uv = (gl_FragCoord.xy * 2.0 - resolution.xy) / min(resolution.x, resolution.y);
    float f = fract(length(uv * 2.1 * fract(time * 0.1)*0.01));

    float l = 1.0 * (length(uv) - time * 0.1);

    uv = gl_FragCoord.xy / resolution.xy;

    vec2 ratio = vec2(
      min((resolution.x / resolution.y) / (channelSize0.x / channelSize0.y), 1.0),
      min((resolution.y / resolution.x) / (channelSize0.y / channelSize0.x), 1.0)
    );

    uv = vec2(
      uv.x * ratio.x + (1.0 - ratio.x) * 0.5,
      uv.y * ratio.y + (1.0 - ratio.y) * 0.5
    );

    uv = mix(uv, uv * sin(l / 0.2 * PI), 0.005);

    vec3 color = texture2D(channel0, uv).xyz;
    color.b = 1.0;

    gl_FragColor = vec4(color, 1.0);
}`

export default {
  name: 'GLSLCanvasContainer',
  components: {GLSLCanvas},
  props: {
    images: Array
  },
  data() {
    return {
      index: 0,
      canvasWidth: 0,
      canvasHeight: 0
    }
  },
  mounted() {
    window.addEventListener("resize", this.onWindowResized)
    this.onWindowResized()

    setInterval(() => {
      this.index++
      if (this.index >= this.images.length) {
        this.index = 0
      }
    }, 5000)
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.onWindowResized)
  },
  computed: {
    currentImage() {
      if (this.images.length === 0) {
        return ""
      }
      return this.images[this.index]
    },
    canvasSize() {
      return {
        width: this.canvasWidth,
        height: this.canvasHeight
      }
    },
    vertexShader() {
      return VERTEX_SHADER_SOURCE
    },
    fragmentShader() {
      return FRAGMENT_SHADER_SOURCE
    }
  },
  methods: {
    onWindowResized() {
      this.canvasWidth = this.$refs.glslCanvasContainer.clientWidth
      this.canvasHeight = this.$refs.glslCanvasContainer.clientHeight
    }
  }
}
</script>

<style scoped lang="stylus">
.glslCanvasContainer
  width 100%
  height 100%

  .glslCanvas
    width 100%
    height 100%
</style>
